<template>
  <div class="pic1">
    <div class="pic" ref='pic'></div>
    <div class="txt">
      <p>{{name}}</p>
      <p>{{value}}</p>
    </div>
  </div>
</template>

<script>
  export default{
    props: ['name','value'],
    mounted(){
      let myChart = this.$echarts.init(this.$refs.pic);
      let colorList = ['#8492e5','#01eaf9','#46b7b5','#0d80cb'];
      let option = {
          tooltip: {
              trigger: 'item',
              formatter: '{a} <br/>{b}: {c} ({d}%)'
          },
          series: [
              {
                  name: '访问来源',
                  type: 'pie',
                  radius: ['50%', '70%'],
                  avoidLabelOverlap: false,
                  label: {
                      show: false,
                      position: 'center'
                  },
                  labelLine: {
                      show: false
                  },
                  itemStyle: {
                      color: (params) => {
                          return colorList[params.dataIndex]
                      }
                  },
                  data: [
                      {value: 67, name: '正常'},
                      {value: 20, name: '故障'},
                      {value: 5, name: '未知'},
                      {value: 2, name: '危险'}
                  ]
              }
          ]
      };

      myChart.setOption(option);



    }
  }
</script>

<style scoped lang='less'>
  .pic{
    width: 100%;
    height: 180px;
    margin-top: 30px;
  }
  .pic1{
    display: inline-block;
    width: 200px;
    position: relative;
    z-index: 999;
    .txt{
      color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -5px;
      margin-left: -30px;
      font-size: 14px;
      p{
        text-align: center;
      }
    }
  }
</style>
